.App{
  min-height: 100vh;
  background-image: linear-gradient(to top, #fdcbf1 0%, #fdcbf1 1%, #e6dee9 100%);
}

.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  text-align: center;
}

.search-input{
  margin-top: 40px;
  width: 50%;
  border: none;
  padding: 10px;
  
}

input:focus {
  outline:none;
}

.search-button{
  margin-top: 40px;
  background: lightcoral;
  border: none;
  padding: 0 10px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  color: white;
}